* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
div,
h1,
h2,
h3,
h4,
h5,
ul,
li,
a,
form {
    margin: 0;
    padding: 0;
    border: 0;
    text-decoration: none;
    font-family: 'Rubik';
}

p {
    font-size: 16px;
    font-weight: 400;
    color: #5C5C5C;
}

button,
input,
textarea {
    outline: none !important;
    transition: all .3s;
    /*font-family: 'Montserrat', sans-serif;*/
}

button::placeholder,
input::placeholder,
textarea::placeholder {
    color: #a8a8a8;
}

input[type=text]:focus,
input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus,
input:active {
    outline: none !important;
}

::-webkit-input-placeholder {
    color: #222;
}

::-moz-placeholder {
    color: #222;
}

/* Firefox 19+ */
:-moz-placeholder {
    color: #222;
}

/* Firefox 18- */
:-ms-input-placeholder {
    color: #222;
}

.transit {
    /* Safari */
    transition: all 0.4s;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance: textfield;
}

html {
    height: 100%;
}

svg {
    flex-shrink: 0;
}

svg,
path {
    transition: all .3s;
}

body {
    /*font-family: 'Montserrat', sans-serif;*/
    font-weight: 400;
    height: 100%;
    overflow-x: hidden;
    color: #1C1C1C;
}

body.fixed-body {
    overflow-y: hidden;
}

body.fixed-body .main__slider-pag {
    display: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
img {
    margin: 0;
    padding: 0;
}

a {
    color: inherit;
    text-decoration: none;
    transition: 0.4s;
}

a:hover {
    text-decoration: none !important;
    color: inherit;
}

a:focus {
    text-decoration: none !important;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
    box-sizing: border-box;
    transition: 0.3s;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: inherit;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

i {
    line-height: 100%;
}

