input[type="date"]::-webkit-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: auto;
    height: auto;
    color: transparent;
    background: transparent;
    opacity: 0;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
    z-index: 1;
}

input[type="date"]::before {
    content: attr(data-placeholder);
    width: 100%;
    color: #5e6162;
}

input[type="date"]:focus::before,
input[type="date"]:valid::before {
    display: none;
}

input::placeholder {
    color: #808080;
}

h1 {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 40px;
    display: flex;
    align-items: flex-end;
    color: #009BE3;
}

.select {
    width: 100%;
    float: unset;
    height: 42px;
    padding: 0;
    border: 1px solid #009BE3 !important;
    border-radius: 10px;
    white-space: unset;
}

.program-select {
    display: none;
}

@media screen and (max-width: 992px) {
    .program-select {
        display: block;
        margin-bottom: 32px;
    }

    .program-group__title {
        margin-bottom: 32px !important;
    }

    .program-items {
        grid-template-columns: 1fr !important;
        row-gap: 6px !important;
    }

    .program-item {
        border-radius: 0 !important;
        border-left: none !important;
        border-right: none !important;
        padding: 15px 0 !important;
    }

    .program-group {
        margin-bottom: 32px !important;
    }

}

.select .current {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 35px 0 15px;
    font-weight: 500;
    font-size: 16px;
    color: #009BE3;
    background-image: url(../img/select-arrow.svg);
    background-repeat: no-repeat;
    background-position: right 17px center;
}

.select .list {
    background: rgba(236, 249, 255, 0.95);
    backdrop-filter: blur(25px);
    margin-top: 0;
    border-radius: 0%;
    width: calc(100% + 2px);
    margin-left: -1px;
    max-height: 380px;
    overflow-y: auto;
}

.select .option {
    border: none;
    padding: 10px 26px;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 145%;
    color: #1C1C1C;
    word-break: break-word;
}

.select .option:hover, .select .option.focus, .select .option.selected.focus {
    background-color: #009BE3;
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    border-radius: 7px;
}

.btn-green {
    background: #40AA29;
    border-radius: 10px;
    height: 47px;
    padding: 0 26px;
    font-weight: bold;
    font-size: 14px;
    color: #FFFFFF;
}

.btn-green:hover {
    background: rgba(64, 170, 41, 0.9);
    color: #FFFFFF;
}

.btn-blue-outline {
    border: 2px solid #009BE3;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #009BE3;
    height: 60px;
    padding: 0 35px;
}

.btn-blue-outline:hover {
    background-color: #009BE3;
    color: #fff;
}

.btn-green-outline {
    border: 1px solid #40AA29;
    padding: 0 24px;
    height: 50px;
    color: #40AA29;
    font-weight: 500;
    font-size: 18px;
}

.btn-green-outline:hover {
    background-color: #40AA29;
    color: #fff;
}

.btn-white {
    background-color: #fff;
    color: #009BE3;
    font-weight: 500;
    font-size: 18px;
    padding: 0 24px;
    height: 50px;
    border-radius: 5px;
}

.btn-white:hover {
    background-color: #e6e6e6;
    color: #009BE3;
}

.date-input {
    background-image: url(../img/calendar.svg);
    background-position: right 20px center;
    background-repeat: no-repeat;
    background-size: 26px auto;
}

@media screen and (max-width: 992px) {
    .date-input {
        background-position: right 8px center;
        background-size: 16px auto;
    }
}

.display2 {
    text-align: center;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 40px;
    color: #009BE3;
}

@media screen and (max-width: 992px) {
    .display2 {
        font-size: 28px;
    }
}

.doctor-card {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-radius: 10px;
}

.doctor-card:hover {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.34);
}

.doctor-card__top {
    position: relative;
    height: 345px;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
}

.doctor-card__top img {
    width: 100%;
    min-height: 100%;
    min-width: 100%;
}

.doctor-card__img {
    display: block;
}

.doctor-card__expirience {
    position: absolute;
    background: #FFFFFF;
    border-radius: 10px;
    height: 44px;
    left: 10px;
    bottom: 10px;
    font-weight: bold;
    font-size: 16px;
    color: #009BE3;
    padding: 0 14px;
    display: flex;
    align-items: center;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    line-height: 19px;
}

.doctor-card__bottom {
    padding: 25px;
    background: #009BE3;
    color: #fff;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 0 0 10px 10px;
}

.doctor-card__name, .doctor-card__prof {
    font-size: 16px;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    line-height: 19px;
    color: #FFFFFF;
}

.doctor-card__prof {
    margin-top: 12px;
    font-weight: 500;
}

.doctor-card__btn {
    margin-top: 10px;
    width: 100%;
}

@media screen and (max-width: 992px) {
    .doctor-card__top {
        height: 280px;
    }

    .doctor-card__bottom {
        padding: 12px;
    }

    .doctor-card__name, .doctor-card__prof {
        font-size: 12px;
        line-height: 15px;
    }

    .doctor-card__prof {
        margin-top: 7px;
    }

    .doctor-card__expirience {
        height: 23px;
        padding: 0 7px;
        left: 6px;
        font-size: 12px;
    }
}

@media screen and (max-width: 576px) {
    .doctor-card__top {
        height: 210px;
    }

    .doctor-card__btn {
        height: 40px;
        padding: 0 5px;
        font-size: 14px;
    }
}

@media screen and (max-width: 360px) {
    .doctor-card__top {
        height: 190px;
    }
}

.feedback-card {
    display: flex;
    height: 155px;
    border: 1px solid #C8EEFF;
    border-radius: 10px;
}

.feedback-card__left {
    flex-grow: 1;
    padding: 26px 100px 26px 34px;
    height: 100%;
}

.feedback-card__link {
    background: #009BE3;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 270px;
    height: 100%;
    font-size: 22px;
    line-height: 27px;
    color: #FFFFFF;
    flex-shrink: 0;
    border-radius: 10px;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
}

.feedback-card__link span {
    text-decoration: none;
}

.feedback-card__link svg {
    margin-left: 16px;
}

.feedback-card__link:hover {
    color: #FFFFFF;
    background: rgba(0, 155, 227, 0.7);
}

.feedback-card__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.feedback-card__top img {
    margin-left: 30px;
    width: 31px;
    height: 34px;
}

.feedback-card__name, .feedback-card__platform {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
}

.feedback-card__name {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #1C1C1C;
}

.feedback-card__platform {
    margin-left: 30px;
    display: flex;
    align-items: center;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #009BE3;
}

.feedback-card__text {
    font-size: 16px;
    line-height: 160%;
    margin-top: 14px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 400;
    color: #1C1C1C;
}

@media screen and (max-width: 992px) {
    .feedback-card__left {
        padding-right: 20px;
    }

    .feedback-card__link {
        width: 240px;
    }
}

@media screen and (max-width: 768px) {
    .feedback-card {
        display: block;
        height: unset;
    }

    .feedback-card__left {
        padding: 0;
        border: none;
    }

    .feedback-card__top {
        display: block;
        position: relative;
        padding: 15px 0 15px 72px;
        border-bottom: 2px solid #E8E8E8;
    }

    .feedback-card__top img {
        position: absolute;
        width: 50px;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        height: 50px;
    }

    .feedback-card__platform {
        margin: 6px 0 0;
    }

    .feedback-card__text {
        -webkit-line-clamp: 4;
        padding: 15px;
    }

    .feedback-card__link {
        margin-top: 25px;
        height: 58px;
        border-radius: 10px;
        padding: 0 18px;
        justify-content: center;
        width: 330px;
    }
}

@media screen and (max-width: 450px) {
    .feedback-card__link {
        width: 100%;
    }
}

.news-card {
    position: relative;
    width: calc(100% - 50px);
    margin-left: auto;
    padding: 33px 50px;
